
.rank {
  padding-top: 100px;
  padding-top: calc(100px + env(safe-area-inset-top));
  // height: 100%;
  box-sizing: border-box;

  .loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .top {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 60px;
    top: env(safe-area-inset-top);
    width: 100%;
    max-width: 750px;
    height: 100px;
    padding: 0 12px;
    box-sizing: border-box;
    background: #fff;
    z-index: 1;

    .calendar {
      position: relative;
      width: 60px;
      height: 60px;
      background: url('~@/assets/images/calendar.png') center no-repeat;
      background-size: 100%;
      transform: translateY(-4px);

      .date {
        position: absolute;
        top: 24px;
        left: 55%;
        transform: translateX(-50%);
        color: #666;
        font-family: Dosis;
        font-size: 24px;
        font-weight: 600;
        letter-spacing: 3px;
      }
    }

    ::v-deep .vc-popover-content-wrapper {
      top: 90px !important;
      left: auto !important;
      right: 14px;
      transform: none !important;

      .vc-popover-caret {
        left: 94% !important;
      }
    }
  }

  .rank-list {
    margin: 0 2px;

    .card-box__wrapper {
      .card-box {
        display: flex;
        flex-direction: row;
      }

      .image-card {
        max-height: 500px;
        margin: 14px 6px;
        border: 1px solid #ebebeb;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .rank {
    .top {
      max-width: 1200px;
    }
  }
}
