.top-bar-wrap {
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 40px;
  width: 100%;
  height: 160px;
  background: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(255,255,255,0));
  z-index: 99;
}
.top-bar {
  background: rgba(0,0,0,0);
}
.top-bar svg {
  font-size: 22px;
  color: #fafafa;
  filter: drop-shadow(2px 4px 4px rgba(0,0,0,0.8));
}
