.user-container {
  height: 100%;
}
.user-container .illust-wrap,
.user-container .user-wrap {
  height: 100vh;
  overflow-y: scroll;
}
.users .info-container .bg-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  overflow: hidden;
}
.users .info-container .bg-cover img {
  display: block;
  width: 100%;
  filter: blur(6px);
}
.users .info-container .info {
  position: relative;
  padding-top: 120px;
  text-align: center;
  font-size: 24px;
}
.users .info-container .info .avatar {
  position: absolute;
  left: 50%;
  top: -100px;
  width: 200px;
  height: 200px;
  transform: translateX(-50%);
}
.users .info-container .info .avatar img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.users .info-container .info .name {
  font-size: 46px;
  font-weight: bold;
  margin: 10px 0;
}
.users .info-container .info .site-list {
  display: flex;
  justify-content: center;
}
.users .info-container .info .site-list.multi .site {
  max-width: 220px;
}
.users .info-container .info .site-list .site {
  margin: 20px 6px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #92a3aa;
}
.users .info-container .info .site-list .site a {
  color: #92a3aa;
}
.users .info-container .info .follow,
.users .info-container .info .friend {
  color: #92a3aa;
  margin: 20px 6px;
}
.users .info-container .info .follow .num,
.users .info-container .info .friend .num {
  color: #333;
  margin-right: 6px;
}
.users .info-container .info .detail {
  position: relative;
  margin: 40px 0;
  padding: 0 12%;
  color: #555;
  line-height: 1.8;
  max-height: 400px;
  overflow: hidden;
  box-sizing: border-box;
}
.users .info-container .info .detail.ex {
  max-height: initial;
}
.users .info-container .info .detail.ex .content::after {
  display: none;
}
.users .info-container .info .detail .content {
  white-space: pre-wrap;
}
.users .info-container .info .detail .content::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 100%;
  height: 50%;
  background: linear-gradient(to top, #fff, rgba(255,255,255,0));
}
.users .info-container .info .detail .more {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}
.users .illusts,
.users .favorite {
  margin: 10px 0 20px 0;
}
/*# sourceMappingURL=src/views/Users/index.css.map */