.image-view {
  position: relative;
  min-height: 600px;
  background-color: #fafafa;
}
.image-view.censored {
  pointer-events: none;
}
.image-view.loaded {
  min-height: unset;
}
.image-view.shrink {
  max-height: 1000px;
  overflow: hidden;
}
.image-view.shrink::after {
  content: '';
  position: absolute;
  top: 40%;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, #fff, rgba(255,255,255,0));
}
.image-view.shrink .dropdown {
  position: absolute;
  bottom: 26px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  color: #fafafa;
  filter: drop-shadow(1px 4px 8px rgba(0,0,0,0.2));
  animation: ani-dropdown 2s ease-in-out infinite;
}
@-moz-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@-webkit-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@-o-keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
@keyframes ani-dropdown {
  0%, 100% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, 6px);
  }
}
.image-view .image-box {
  position: relative;
  background: #fafafa;
  max-height: 100vh;
  margin-bottom: 20px;
}
.image-view .image-box:nth-of-type(n+2) {
  min-height: 600px;
}
.image-view .image-box .image {
  display: block;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  object-fit: contain;
  cursor: zoom-in;
}
.image-view .image-box .image[lazy='loading'] {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  min-height: auto;
}
.image-view .image-box .ugoira {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.image-view .ugoira-controls {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.image-view .ugoira-controls .btn-play,
.image-view .ugoira-controls .btn-pause {
  position: absolute;
  right: 16px;
  bottom: 16px;
  color: rgba(122,172,208,0.9);
}
.image-view .ugoira-controls .progress-bar {
  position: absolute;
  bottom: 0;
  width: 0;
  height: 4px;
  overflow: hidden;
  transition: width 0.1s;
}
.image-view .ugoira-controls .progress-bar .background {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(to right, #3fffa2 0%, #1a9be0 100%);
}
