
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 750px;
  margin: 0 auto;

  &.show-nav {
    .back-top {
      bottom: 130px;
    }
  }

  .back-top {
    position: fixed;
    right: 40px;
    bottom: 40px;
    cursor: pointer;

    .icon-top {
      width: 100px;
      height: 100px;
    }
  }
}

@media screen and (min-width: 768px) {
  #app {
    max-width: 1200px;
  }
}
