.main-layout {
  height: 100%;
  box-sizing: border-box;
}
.main-layout.safe-area {
  padding-top: 60px;
  height: calc(100vh - 60px);
  padding-top: env(safe-area-inset-top);
  height: calc(100vh - env(safe-area-inset-top));
}
.app-main {
  position: relative;
}
.app-main::-webkit-scrollbar {
  width: 0;
}
